import styled from 'styled-components'
import {useEffect,useState} from 'react'
import {Link} from 'react-router-dom'

function App() {
  const [data,setData]=useState([]);
  const [flag,setFlag]=useState(false)


  
  useEffect(()=>{
    async function fetchItems(){
        try{
            const API_URL="https://s3-ap-southeast-1.amazonaws.com/he-public-data/newsf6e2440.json";
            const response=await fetch(API_URL);
            const data=await response.json();
            console.log(data);
            setData(data.sort((a,b)=>b.TIMESTAMP-a.TIMESTAMP));
            setFlag(!flag);
            console.log(`Value inside state is ${data}`)
        }
        catch(err){
          console.log(err.message);
        }
    }
    fetchItems();
  },[])
  return (
    
    <Container>
     <Header>Welcome to New Portal Home Page</Header> 
     
      <Content>
      {flag && data.map((item,index)=>{
        return (
          <Wrap href={item.URL}>
         <button>
         {item.PUBLISHER}<br/><br/>
         {new Date(item.TIMESTAMP).toISOString()}
         </button>
         </Wrap>
          
        )
      })}
      </Content>
      
    </Container>
    
  );
}

export default App;

const Content=styled.div`
  margin-top:40px;
  display:grid;
  grid-gap:25px;
  
  grid-template-columns:repeat(3,minmax(0,1fr));
  position:relative;
  padding:20px 80px 20px;

  button{
    font-size:1.5rem;
    border-radius:10px;
    color:black;
    background:#ecf0f1;
    padding:20px 40px 20px;

    &:hover{
      transform:scale(1.05);
      border:3px solid black;
      transition:all 0.1s ease-in 0s;
    }
    
  }
 
  
  `

  const Container=styled.div`
    width:100%;
    height:100%;
    position:relative;
    background:#2c3e50;
    color:white;
  `



const Header=styled.div`
  padding:40px;
  display:flex;
  align-items:center;
  justify-content:center;
  font-size:2rem;
  font-weight:bold;
  background:#2c3e50;
  color:white;
`

const Wrap=styled.a`

`

